import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { AuthService } from '../service/auth.service';
import { AlertService } from '../service/alert.service';
import { Router, ActivatedRoute } from '@angular/router';
import { SharedService } from '../service/shared.service';
import { ScriptLoaderService, ScriptModel } from '../service/script-loader.service';


@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {

  form: FormGroup;
  loading = false;
  submitted = false;
  responseMessage:any;
  isTokenChecked:boolean=false;

  constructor (
    private formBuilder: FormBuilder,
    private authService: AuthService,
    private alertService: AlertService,
    private router: Router,
    private sharedService:SharedService,
    private route:ActivatedRoute,
    private scriptLoader: ScriptLoaderService,
  ) {
    const particlesJSScript : ScriptModel= { name: 'particles.js', type: 'text/javascript', src: 'assets/js/particles.min.js' };
    this.scriptLoader.load(particlesJSScript).subscribe(()=>{
      const body = <HTMLDivElement> document.body;
      const script = document.createElement('script');
      script.innerHTML = '';
      script.src = "../assets/js/particle-background.js";
      script.async = false;
      script.defer = true;
      body.appendChild(script);
    });
  }

  async ngOnInit() {
    await this.checkIfResetPasswordToken();

    this.responseMessage='';
    this.form = this.formBuilder.group({
        passwordGroup: this.formBuilder.group({
          password: ['', [Validators.required, Validators.pattern('^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$')]],
          confirmPassword: ['']
        }, {validator: this.checkPasswords })
    });

  }
  
  checkPasswords(group: FormGroup) { // here we have the 'passwords' group
    let pass = group.get('password').value;
    let confirmPass = group.get('confirmPassword').value;

    return pass === confirmPass ? null : { notSame: true }     
  }
  get pg() { return this.form.controls.passwordGroup as FormGroup;; }

  onSubmit() {
      this.submitted = true;
      
      this.alertService.clear();
      
      if (this.form.invalid) {
          return;
      }
      const resetData = {
        password: this.form.value.passwordGroup.password,
        token: this.router.url.split('/')[2]
      }
      this.authService.resetPassword(resetData)
      .pipe(first())
      .subscribe({
          next: () => {
              this.responseMessage='Password reset successfully.';
              //this.alertService.success('Password reset successfully.', { keepAfterRouteChange: true, autoClose: true });
              this.router.navigate(['/reset-success']);
          },
          error: error => {
             this.responseMessage=error.error.message;
              //this.alertService.error(error.error.message, {autoClose: true});
              this.loading = false;
          }
      });
  }


  async checkIfResetPasswordToken(){
    let token=this.route.snapshot.params.id;
    const response = await this.sharedService.postDataPromise('auth/checkResetPasswordToken',{token:token});
      if(!response.data){
        this.router.navigate(['/404']);
      }else{
        this.isTokenChecked=true;
      }   
  }

}
