import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { FooterComponent } from './footer/footer.component';
import { HeaderComponent } from './header/header.component';
import { ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { ConfirmemailComponent } from './confirmemail/confirmemail.component';
import { ResendVerificationEmailComponent } from './resend-verification-email/resend-verification-email.component';
import { ResetPasswordComponent } from './reset-password/reset-password.component';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';
import { GlobeComponent } from './animation/globe/globe.component';
import { TncComponent } from './tnc/tnc.component';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ToastrModule } from 'ngx-toastr';

import { AuthHeaderInterceptor } from './interceptors/header.interceptor';
import { UserSignInComponent } from './sign-in/user-sign-in.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { GettingStartedComponent } from './getting-started/getting-started.component';
import { NavigationLayoutComponent } from './layouts/navigation-layout/navigation-layout.component';
import { DefaultLayoutComponent } from './layouts/default-layout/default-layout.component';
import { HomeComponent } from './home/home.component';

@NgModule({
  declarations: [
    AppComponent,
    FooterComponent,
    HeaderComponent,
    ConfirmemailComponent,
    ResendVerificationEmailComponent,
    ResetPasswordComponent,
    GlobeComponent,
    TncComponent,
    UserSignInComponent,
    GettingStartedComponent,
    NavigationLayoutComponent,
    DefaultLayoutComponent,
    HomeComponent
  ],
  imports: [
    BrowserModule.withServerTransition({ appId: 'serverApp' }),
    AppRoutingModule,
    ReactiveFormsModule,
    HttpClientModule,
    ServiceWorkerModule.register('ngsw-worker.js', { enabled: environment.production }),
    BrowserAnimationsModule, // required animations module
    ToastrModule.forRoot(), NgbModule
  ],
  providers: [{
    provide: HTTP_INTERCEPTORS,
    useClass: AuthHeaderInterceptor,
    multi: true,
  }],
  bootstrap: [AppComponent],
  entryComponents: [GlobeComponent]
})
export class AppModule { }
