import { NgModule } from '@angular/core';
import { Routes, RouterModule, PreloadAllModules } from '@angular/router';
import { ConfirmemailComponent } from './confirmemail/confirmemail.component';
import { AuthGuard } from './_helpers';
import { ResendVerificationEmailComponent } from './resend-verification-email/resend-verification-email.component';
import { ResetPasswordComponent } from './reset-password/reset-password.component';
import { HomeComponent } from './home/home.component';
import { TncComponent } from './tnc/tnc.component';
import { UserSignInComponent } from './sign-in/user-sign-in.component';
import { InviteSignUpComponent } from './sign-up/invite-sign-up.component';
import { AdminSignInComponent } from './sign-in/admin-sign-in.component';
import { DefaultLayoutComponent } from './layouts/default-layout/default-layout.component';
import { NavigationLayoutComponent } from './layouts/navigation-layout/navigation-layout.component';


const routes: Routes = [

  {
    path: '',
    component: NavigationLayoutComponent,
    children: [
      {
        path: '',
        component: HomeComponent,
        data: {
          title: 'J U P I T A | The Communications Analytics Tool For Any Size Enterprise'
        }
      },

      {
        path: 'docs',
        loadChildren: () => import('./document/document.module').then(m => m.DocumentModule)
      },

      {
        path: 'pricing',
        loadChildren: () => import('./pricing/pricing.module').then(m => m.PricingModule)
      },
      // {
      //   path: 'products',
      //   loadChildren: () => import('./product/product.module').then(m => m.ProductModule)
      // },

      {
        path: 'termsandconditions/:id',
        component: TncComponent,
        data: {
          title: 'J U P I T A | Terms & Conditions',
          metaTitle: 'Terms & Conditions',
          metaDescription: 'Your privacy is important to us. It is Jupita’s policy to respect your privacy regarding any information we may collect while using this application. Accordingly, we have developed this privacy policy in order for you to understand how we collect, use, communicate, disclose and otherwise make use of personal information.'
        }
      },
    ]

  },
  {
    path: '',
    component: DefaultLayoutComponent,
    children: [
      // {
      //   path: 'user',
      //   loadChildren: () => import('./user/user.module').then(m => m.UserModule),
      //   canActivate: [AuthGuard]
      // },
      {
        path: 'admin',
        loadChildren: () => import('./admin/admin.module').then(m => m.AdminModule),
      },
      {
        path: 'sign-up',
        loadChildren: () => import('./sign-up/sign-up.module').then(m => m.SignUpModule)
      },
      {
        path: 'sign-in',
        loadChildren: () => import('./sign-in/sign-in.module').then(m => m.SignInModule)
      },
      {
        path: 'admin-login',
        component: AdminSignInComponent,
        data: {
          title: 'J U P I T A | Hello Alex'
        }
      },
      {
        path: 'forgot-password',
        loadChildren: () => import('./forgot-password/forgot-password.module').then(m => m.ForgotPasswordModule)
      },
      {
        path: 'confirmation/:id',
        component: ConfirmemailComponent,
        data: {
          title: 'J U P I T A | Confirm Email Address'
        }
      },
      {
        path: 'invite-signup/:email',
        component: InviteSignUpComponent,
        data: {
          title: 'J U P I T A | Welcome'
        }
      },
      {
        path: 'resend-verification-email/:id',
        component: ResendVerificationEmailComponent,
        data: {
          title: 'J U P I T A | Confirm Email Address'
        }
      },
      {
        path: 'reset/:id',
        component: ResetPasswordComponent,
        data: {
          title: 'J U P I T A | Reset Password'
        }
      },
      {
        path: 'reset-success',
        component: UserSignInComponent,
        data: {
          title: 'J U P I T A | Reset Success'
        }
      },
      {
        path: 'email-verified/:email',
        component: UserSignInComponent,
        data: {
          title: 'J U P I T A | Welcome'
        }
      },
      {
        path: 'page-not-found',
        loadChildren: () => import('./page-not-found/page-not-found.module').then(m => m.PageNotFoundModule)
      },
      {
        path: '**',
        loadChildren: () => import('./page-not-found/page-not-found.module').then(m => m.PageNotFoundModule)
      },
      {
        path: '404',
        redirectTo: '**',
        pathMatch: 'full'
      },
    ]

  },

  {
    path: '',
    redirectTo: '',
    pathMatch: 'full'
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes,
    {
      // preloadingStrategy: PreloadAllModules,
      initialNavigation: 'enabledBlocking', // https://angular.io/api/router/InitialNavigation
      relativeLinkResolution: 'legacy'
    })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
