import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { AuthService } from '../service/auth.service';
import { AlertService } from '../service/alert.service';
import { Router,ActivatedRoute } from '@angular/router';
import { SharedService } from '../service/shared.service';
import { ScriptLoaderService, ScriptModel } from '../service/script-loader.service';


@Component({
  selector: 'app-invite-sign-up',
  templateUrl: './invite-sign-up.component.html',
  styleUrls: ['./sign-up.component.scss']
})
export class InviteSignUpComponent implements OnInit {

  form: FormGroup;
  loading = false;
  submitted = false;
  existEmailError:any;
  invitedEmail:any;
  isTokenChecked:boolean=false;

  constructor (
    private formBuilder: FormBuilder,
    private authService: AuthService,
    private alertService: AlertService,
    private router: Router,
    private actRoute: ActivatedRoute,
    private sharedService:SharedService,
    private scriptLoader: ScriptLoaderService,
    ) {
    const particlesJSScript: ScriptModel = { name: 'particles.js', type: 'text/javascript', src: 'assets/js/particles.min.js' };
    this.scriptLoader.load(particlesJSScript).subscribe(() => {
      const body = <HTMLDivElement>document.body;
      const script = document.createElement('script');
      script.innerHTML = '';
      script.src = "../assets/js/particle-background.js";
      script.async = false;
      script.defer = true;
    body.appendChild(script);
      });
    this.invitedEmail = this.actRoute.snapshot.params.email;
  }

  async ngOnInit(){
    await this.checkInviteEmailStatus();

    this.existEmailError='';
    this.form = this.formBuilder.group({
        email: [this.invitedEmail, Validators.compose([Validators.required])],
        fullName: ['', Validators.required],
        passwordGroup: this.formBuilder.group({
          password: ['', [Validators.required, Validators.pattern('^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$')]],
          confirmPassword: ['']
        }, {validator: this.checkPasswords })
    });
  }
  
  checkPasswords(group: FormGroup) { // here we have the 'passwords' group
    let pass = group.get('password').value;
    let confirmPass = group.get('confirmPassword').value;

    return pass === confirmPass ? null : { notSame: true }     
  }
  
  get f() { return this.form.controls; }
  get pg() { return this.form.controls.passwordGroup as FormGroup;; }

  onSubmit() {
      this.submitted = true;
      
      this.alertService.clear();
      
      if (this.form.invalid) {
          return;
      }
      
      this.form.value.isInvited=true;
      this.authService.register(this.form.value)
      .pipe(first())
      .subscribe({
          next: (response) => {
            if(response.is_success){
                this.router.navigate(['/user']);
                 localStorage.setItem("user",JSON.stringify(response.data));
            }else{
                this.router.navigate(['/404']);
            }
              // this.alertService.success('Registration successful, please verify your email address to login.', { keepAfterRouteChange: true, autoClose: true });
              
          },
          error: error => {
              // this.alertService.error(this.alertService.AlertMessages.EmailAlreadyExists, {autoDismiss: false});
              // this.loading = false;
            this.existEmailError=this.alertService.AlertMessages.EmailAlreadyExists;
          }

      });
  }

  async checkInviteEmailStatus(){
    let token=this.invitedEmail;
    const response= await this.sharedService.postDataPromise('auth/checkInviteEmail',{email:token});
      if(!response.data){
        this.router.navigate(['/404']);
      }else{
        this.isTokenChecked=true;
      }   
  }

}
